#nav {
    width: auto;
    -webkit-box-shadow: 0px 0px 10px 0px black;
            box-shadow: 0px 0px 10px 0px black;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    background: #bdc3c77c;
}
#nav h1 {
    margin: 0;
}
#nav h1 a {
    text-decoration: none;
    color: #2c3e50;
}
#nav img {
    width: 3%;
}
#nav .nav__links {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: 20%;
}
#nav .nav__links a {
    color: #2c3e50;
    text-decoration: none;
    font-weight: 100;
}

@media screen and (max-width: 800px) {
    #nav img {
        width: 13%;
    }
    #nav .nav__links {
        width: auto;
    }
    #nav .nav__links a {
        padding: 0 4px;
    }    
}


